import React from 'react';
import { styled as MuiStyled, useTheme } from '@material-ui/core/styles';
import { compose, sizing, spacing, palette } from '@material-ui/system';
import { Box, Divider as MuiDivider } from '@material-ui/core';
import ReactMarkdown from 'react-markdown';
import { Link as GLink } from 'gatsby';
import styled from 'styled-components';
import { PrismAsync as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow as codeStyle } from 'react-syntax-highlighter/dist/esm/styles/prism';
/**
 * @description Wrapper with text-wrap breaking on word
 * @param {any} props - material-ui/system props
 */
export const TextWrappable = MuiStyled(Box)({
  overflowWrap: 'break-word',
  wordBreak: 'break-word',
});

/**
 * divider with spacing props
 */
export const Divider = MuiStyled(MuiDivider)(spacing);

/**
 * gatsby Link component without underline
 */
export const TextLink = MuiStyled((p) => (
  <GLink style={{ boxShadow: 'none', textDecoration: 'none' }} {...p} />
))(compose(palette));

/**
 * @description HTML img with additional spacing and sizing props
 */
export const Img = MuiStyled('img')(compose(sizing, spacing));

/**
 * @description HTML blockquote with nice styling
 */
export const Quote = (props) => {
  const theme = useTheme();
  const StyledQuote = styled.div`
    font-family: 'Source Serif Pro', serif;
    font-style: italic;
    font-weight: bold;
    background-color: ${theme.palette.grey[200]};
    border-left: 5px solid ${theme.palette.grey[700]};
    margin: ${theme.spacing(2, 4)};
    padding: ${theme.spacing(2)}px;
    &::before {
      color: ${theme.palette.grey[400]};
      content: open-quote;
      font-size: ${theme.typography.h1.fontSize};
      line-height: 0.1em;
      margin-right: 0.25em;
      vertical-align: -0.4em;
    }
    & p {
      display: inline;
      margin: 0;
    }
    & p.author {
      display: block;
      text-align: right;
      margin: 0;
    }
  `;
  return <StyledQuote {...props} />;
};

export const CodeBlock = ({ language, value }) => {
  return (
    <SyntaxHighlighter language={language} style={codeStyle}>
      {value}
    </SyntaxHighlighter>
  );
};

/**
 * ReactMarkdown wrapper with custom Quote and Code block components
 */
export const Md = (props) => {
  return (
    <ReactMarkdown
      renderers={{ blockquote: Quote, code: CodeBlock }}
      {...props}
    />
  );
};
