/**
 * @see https://www.figma.com/file/u4RZEOmFokGWfATLyYsOfK/thrd.space-UI-library-v2?node-id=0%3A1
 * For Roboto font specs:
 * @see https://fonts.google.com/specimen/Roboto
 */
export const LIGHT = 300;
export const REGULAR = 400;
export const MEDIUM = 500;
export const BOLD = 800;

const defaultFontSize = 16;

const pxToRem = (px) => `${px / defaultFontSize}rem`;

const typography = {
  fontFamily: '"Merriweather Sans", "Helvetica", "Arial", sans-serif',
  htmlFontSize: defaultFontSize,
  fontSize: defaultFontSize,
  fontWeightLight: LIGHT,
  fontWeightRegular: REGULAR,
  fontWeightMedium: MEDIUM,
  fontWeightBold: BOLD,
  h1: {
    fontSize: pxToRem(96),
    fontWeight: LIGHT,
  },
  h2: {
    fontSize: pxToRem(60),
    fontWeight: LIGHT,
  },
  h3: {
    fontSize: pxToRem(42),
    fontWeight: REGULAR,
  },
  h4: {
    fontSize: pxToRem(30),
    fontWeight: REGULAR,
  },
  h5: {
    fontSize: pxToRem(24),
    fontWeight: REGULAR,
  },
  h6: {
    fontSize: pxToRem(20),
    fontWeight: MEDIUM,
  },
  subtitle1: {
    fontSize: pxToRem(16),
    fontWeight: REGULAR,
  },
  subtitle2: {
    fontSize: pxToRem(14),
    fontWeight: MEDIUM,
  },
  body1: {
    fontSize: pxToRem(16),
    fontWeight: REGULAR,
  },
  body2: {
    fontSize: pxToRem(14),
    fontWeight: REGULAR,
  },
  button: {
    fontSize: pxToRem(16),
    fontWeight: MEDIUM,
    textTransform: 'None',
  },
  caption: {
    fontSize: pxToRem(12),
    fontWeight: REGULAR,
  },
  overline: {
    lineHeight: pxToRem(12),
    fontSize: pxToRem(12),
    fontWeight: REGULAR,
    textTransform: 'capitalize',
  },
};

export default typography;
