import React from 'react';
import { styled } from '@material-ui/core/styles';
import Text from '@material-ui/core/Typography';
import { compose, spacing, typography, palette } from '@material-ui/system';

const composed = compose(spacing, typography, palette);

/**
 * @description h1 typography variant
 * @param {any} props - Any typography or material-ui/system spacing props
 */
export const H1 = styled((p) => <Text variant="h1" {...p} />)(composed);

